/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.eltdf-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 30px;
        float: left;
    }

    .woocommerce-result-count {
    	color: #333;
    	font-size: 17px;
    	font-weight: 800;
    	font-family: $second-font;
        line-height: 44px;
    }

    .woocommerce-ordering {
        text-align: right;

        .orderby {
            width: auto;
            min-width: 250px;
            margin: 0;
        }
    }
}
/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */